'use strict';


var React = require('react');
var Bx = require('react-bootstrap');
var Rx = require('react-router');


var AppCtrl = React.createClass({
	render: function() {
		var message = 'Welcome to the app!';
		return (
			<div className='container'>
				<Bx.Navbar brand={<Rx.Link to='dashboard'>Scaffle</Rx.Link>}>
					<Bx.Nav>
						<Bx.DropdownButton title='Collections'>
							<Bx.MenuItem>Cinemas</Bx.MenuItem>
							<Bx.MenuItem>Movies</Bx.MenuItem>
							<Bx.MenuItem>Visits</Bx.MenuItem>
						</Bx.DropdownButton>
						<Bx.NavItem href='#'>Schemas</Bx.NavItem>
						<Bx.NavItem href='#'>Files</Bx.NavItem>
						<Bx.NavItem href='#'>Users</Bx.NavItem>
					</Bx.Nav>
				</Bx.Navbar>
				<Rx.RouteHandler/>
			</div>
		);
	}
});


var DashboardCtrl = React.createClass({
	render: function() {
		return (
			<div>
				<div className="page-header">
					<h1>Dashboard</h1>
				</div>
				<p><Rx.Link to='about'>Go to the About page &raquo;</Rx.Link></p>
				<p><Rx.Link to='collection-index'>Go to the Collections page &raquo;</Rx.Link></p>
			</div>
		);
	}
});


var AboutCtrl = React.createClass({
	render: function() {
		return (
			<div>
				<div className="page-header">
					<h1>About</h1>
				</div>
				<p><Rx.Link to='dashboard'>Go to the Dashboard page &raquo;</Rx.Link></p>
			</div>
		);
	}
});



var EntityRow = React.createClass({
	render: function() {
		return (
			<tr>
				<td>Cell</td>
			</tr>
		);
	}
});


var GetCollectionCtrl = React.createClass({
	render: function() {
		var numrows = 10;
		return (
			<div>
				<div className="page-header">
					<h1>Collections</h1>
				</div>
				<p><Rx.Link to='dashboard'>Go to the Dashboard page &raquo;</Rx.Link></p>
				<Rx.Table bordered striped>
					<EntityRow/>
					<EntityRow/>
					<EntityRow/>
					<EntityRow/>
					<EntityRow/>
				</Rx.Table>
			</div>
		);
	}
});


var GetEntitiesCtrl = React.createClass({
	render: function() {
		return (
			<div>
				<div className="page-header">
					<h1>Cinemas</h1>
				</div>
				<p><Rx.Link to='dashboard'>Go to the Dashboard page &raquo;</Rx.Link></p>
			</div>
		);
	}
});


var GetEntityCtrl = React.createClass({
	render: function() {
		return (
			<div>
				<div className="page-header">
					<h1>Cinema</h1>
				</div>
				<p><Rx.Link to='dashboard'>Go to the Dashboard page &raquo;</Rx.Link></p>
			</div>
		);
	}
});


var routes = (
	<Rx.Route handler={AppCtrl}>
		<Rx.Route name='dashboard' path='/' handler={DashboardCtrl} />
		<Rx.Route name='about' path='about-us' handler={AboutCtrl} />
		<Rx.Route name='collection-index' path='/collections' handler={GetCollectionCtrl} />
		<Rx.Route name='collection-list' path='collections/:collection' handler={GetEntitiesCtrl} />
		<Rx.Route name='collection-view' path='collections/:collection/:id' handler={GetEntityCtrl} />
	</Rx.Route>
);


Rx.run(routes, Rx.HashLocation, function(RootEl){
  React.render(React.createElement(RootEl), document.getElementById('root'));
});

